.card {
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 0 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__info-wrapper {
  padding: 40px 16px 0;
}

.card__title {
  font-style: inherit;
  width: 100%;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card__code {
  text-align: left;
  font-style: inherit;
  color: #616070;
  margin: 0;
  padding: 4px 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 23px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  display: flex;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price {
  font-weight: 700;
  line-height: 18px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: block;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.review {
  text-align: right;
  font-size: 10px;
  line-height: 17px;
  display: block;
}

* {
  box-sizing: border-box;
}

body {
  color: #060b35;
  background-color: #f3f3f3;
  margin: 0;
  font-family: Roboto, sans-serif;
}

